.footer {
  background-image: url(../images/footer.png);
  width: 100%;
  height: 200px;
  background-size: cover;
  background-position: right;
  position: sticky;
  z-index: -1;
  bottom: 0;

  @include onTablet {
    height: 480px;
  }

  @include onDesctop {
    background-position: top;
    height: 555px;
  }
}
